import { useDispatch } from 'react-redux';
import { useMemo } from 'react';
import {
    resetChecklistCheckedItems,
} from 'state/actions/checklist';
import { useChecklistInit } from 'components/checklists/useChecklistInit';
import { useChecklistMenuItems } from 'components/checklists/useChecklistMenuItems';
import { useChecklistItems } from 'components/checklists/useChecklistItems';
import { ChecklistsData } from 'lib/checklist/data/checklistsData';
import { ChecklistListsData } from 'lib/checklist/data/listsData';

export const useChecklist = ({
    checklistName, checklistListName, selectedFilters, checklistURLManager,
}) => {
    const dispatch = useDispatch();

    const checklistData = useMemo(() => ChecklistsData[checklistName], [checklistName]);

    const { filteredChecklistLists, selectedChecklistListName } = useChecklistInit({
        checklistName,
        checklistListName,
        checklistData,
        selectedFilters,
    });

    const checklistListData = useMemo(
        () => ChecklistListsData[selectedChecklistListName],
        [selectedChecklistListName],
    );

    const menuItems = useChecklistMenuItems({
        checklistName,
        checklistURLManager,
        filteredChecklistLists,
    });

    const items = useChecklistItems({
        checklistName,
        selectedChecklistListName,
        filteredChecklistLists,
    });

    const resetCallback = useMemo(() => () => dispatch(resetChecklistCheckedItems(checklistName)), [checklistName]);

    return {
        selectedChecklistListName,
        checklistData,
        checklistListData,
        menuItems,
        items,
        resetCallback,
    };
};
