/* eslint-disable react/jsx-props-no-spreading */
import React, { useMemo } from 'react';
import { Menu } from 'antd';
import { useHistory, useLocation } from 'react-router';
import { styled } from 'styletron-react';
import { objectMap } from 'lib/objectMap';

const MenuContainer = styled('div', {
    display: 'inline-block',
});

const useNavbarMenus = (mapping) => {
    const { pathname } = useLocation();
    const history = useHistory();

    const menuProps = useMemo(() => objectMap(mapping, (url) => ({
        key: url,
        isSelected: pathname.startsWith(url),
        onClick: () => history.push(url),
    })), [mapping, pathname]);

    const selectedKeys = useMemo(
        () => Object.values(menuProps)
            .filter((value) => value.isSelected)
            .map((value) => value.key),
        [menuProps],
    );

    return { menuProps, selectedKeys };
};

export const Navbar = () => {
    const {
        menuProps: { wtCJ4MenuItemProps },
        selectedKeys,
    } = useNavbarMenus({
        wtCJ4MenuItemProps: '/checklists/workingtitle-cj4',
    });

    return (
        <MenuContainer>
            <Menu theme="dark" mode="horizontal" selectedKeys={selectedKeys}>
                <Menu.Item {...wtCJ4MenuItemProps}>WT CJ4 Checklist</Menu.Item>
            </Menu>
        </MenuContainer>
    );
};

Navbar.propTypes = {};
