import React, { useMemo, useState } from 'react';
import {
    Button, Col, Divider, List, Menu, Modal, Progress, Row, Switch, Tag, Tooltip, Typography,
} from 'antd';
import { styled, useStyletron, withStyle } from 'styletron-react';
import { Clearfix } from 'components/lib/Clearfix';
import PropTypes from 'prop-types';
import { useChecklist } from 'lib/checklist/useChecklist';
import {
    CheckOutlined,
    CloseOutlined,
    FullscreenExitOutlined,
    FullscreenOutlined,
    QuestionCircleOutlined,
    UnorderedListOutlined,
} from '@ant-design/icons';
import { isNotNullOrUndefined } from 'lib/isNullOrUndefined';
import { ChecklistFiltersData } from 'lib/checklist/data/filtersData';
import { useChecklistURLConfig } from 'components/checklists/useChecklistURLConfig';
import { useWindowWidthConditions } from 'lib/hooks/useWindowDimensions';
import { useChecklistLeftHandedMode } from 'components/checklists/useChecklistLeftHandedMode';
import { useChecklistFullscreenMode } from 'components/checklists/useChecklistFullscreenMode';

const { Title, Text } = Typography;

const MenuTitle = styled('div', {
    float: 'left',
    fontWeight: 'bold',
    textTransform: 'uppercase',
});

const ProgressBackground = styled('div', {
    backgroundColor: 'rgba(255, 255, 255, .75)',
    paddingRight: '5px',
    paddingLeft: '5px',
    float: 'right',
});

const ChecklistMenuTitle = ({ title, percentProgress }) => {
    let progress = null;

    if (percentProgress !== '0') {
        progress = (
            <ProgressBackground>
                <Progress type="circle" width={35} strokeWidth={8} percent={percentProgress} />
            </ProgressBackground>
        );
    }

    return (
        <>
            <MenuTitle>
                {title}
            </MenuTitle>
            {progress}
            <Clearfix />
        </>
    );
};

ChecklistMenuTitle.propTypes = {
    title: PropTypes.string.isRequired,
    percentProgress: PropTypes.number.isRequired,
};

const ChecklistItemTitle = styled('div', {
    display: 'inline-block',
    fontSize: '1.2em',
    fontWeight: 'bold',
    color: 'rgb(54,54,54)',
});

const ChecklistItemHelp = styled('sup', {
    display: 'inline-block',
    marginLeft: '1em',
    fontSize: '1em',
    verticalAlign: 'middle',
});

const ChecklistItemState = styled('div', {
    display: 'inline-block',
    fontSize: '1.2em',
    fontWeight: 'bolder',
    color: 'black',
});

const ChecklistItemWrapperBlank = styled('div', {
    padding: '1em',
    borderBottom: '1px solid rgb(45, 45, 45)',
    ':hover': {
        backgroundColor: 'rgba(108,233,255,0.1)',
    },
});

const ChecklistItemWrapperGreen = withStyle(ChecklistItemWrapperBlank, {
    backgroundColor: 'rgba(108,255,108,0.1)',
    ':hover': {
        backgroundColor: 'rgba(108,255,108,0.25)',
    },
});

const getChecklistItemWrapper = ({ isChecked }) => {
    if (isChecked) {
        return ChecklistItemWrapperGreen;
    }
    return ChecklistItemWrapperBlank;
};

const ChecklistItem = ({
    item: {
        uid, title, state, isChecked, toggleChecked, moreInfoShort, moreInfoLong, isNotImplemented, tags,
    },
}) => {
    const { leftHandedMode } = useChecklistLeftHandedMode();

    const [css] = useStyletron();
    const [isModalVisible, setIsModalVisible] = useState(false);

    let textClassName = null;
    if (isNotImplemented) {
        textClassName = css({ opacity: '50%' });
    }

    const { showModal, handleOk } = useMemo(() => ({
        showModal: () => {
            setIsModalVisible(true);
        },
        handleOk: () => {
            setIsModalVisible(false);
        },
    }), [setIsModalVisible]);

    let moreInfoHelp = null;
    let moreInfoRow = null;

    if (isNotNullOrUndefined(moreInfoShort)) {
        moreInfoHelp = (
            <ChecklistItemHelp onClick={showModal}>
                <Tooltip
                    title={moreInfoShort}
                    placement="bottomRight"
                    overlayStyle={{ maxWidth: '50vw' }}
                    color="#3f3f3f"
                >
                    <QuestionCircleOutlined />
                </Tooltip>
            </ChecklistItemHelp>
        );

        moreInfoRow = (
            <Modal
                title={(
                    <>
                        <strong>Help:</strong>
                        {' '}
                        {title}
                        :
                        {' '}
                        {state}
                    </>
                )}
                visible={isModalVisible}
                onOk={handleOk}
                onCancel={handleOk}
                cancelButtonProps={{ style: { display: 'none' } }}
                maskClosable
                width="60vw"
            >
                {isNotNullOrUndefined(moreInfoLong) ? moreInfoLong : moreInfoShort}
            </Modal>
        );
    }

    const ChecklistItemWrapper = getChecklistItemWrapper({ isChecked });

    const tagsValues = Array.from(tags)
        .filter((tag) => !tag.hidden)
        .map((tag) => <Tag className={textClassName} color={tag.color}>{tag.uid}</Tag>);

    const [tagsCols, titleCols, stateCols, switchCols] = useWindowWidthConditions([
        { limit: 800, result: [0, 13, 9, 2] },
        { limit: 1200, result: [2, 12, 8, 2] },
        { limit: 1500, result: [2, 14, 6, 2] },
        { result: [2, 16, 5, 1] },
    ]);

    const switchColumn = (
        <Col span={switchCols} onClick={toggleChecked}>
            <Switch
                checkedChildren={<CheckOutlined />}
                unCheckedChildren={<CloseOutlined />}
                checked={isChecked || isNotImplemented}
                disabled={isNotImplemented}
            />
        </Col>
    );

    const tagsColumn = <Col span={tagsCols}>{tagsValues}</Col>;

    let rightColumn = null;
    let leftColumn = null;

    if (leftHandedMode) {
        leftColumn = switchColumn;
        rightColumn = tagsColumn;
    } else {
        leftColumn = tagsColumn;
        rightColumn = switchColumn;
    }

    return (
        <ChecklistItemWrapper key={uid}>
            <Row>
                {leftColumn}
                <Col span={titleCols}>
                    <ChecklistItemTitle className={textClassName}>
                        {title}
                    </ChecklistItemTitle>
                </Col>
                <Col span={stateCols}>
                    <ChecklistItemState className={textClassName}>{state}</ChecklistItemState>
                    {moreInfoHelp}
                </Col>
                {rightColumn}
            </Row>

            {moreInfoRow}
        </ChecklistItemWrapper>
    );
};

ChecklistItem.propTypes = {
    item: PropTypes.shape({
        uid: PropTypes.string.isRequired,
        title: PropTypes.string.isRequired,
        state: PropTypes.string.isRequired,
        isChecked: PropTypes.bool.isRequired,
        toggleChecked: PropTypes.func.isRequired,
        moreInfoShort: PropTypes.string.isRequired,
        moreInfoLong: PropTypes.string.isRequired,
        isNotImplemented: PropTypes.bool.isRequired,
        tags: PropTypes.arrayOf(PropTypes.string).isRequired,
    }).isRequired,
};

const ChecklistConfigurationSwitch = ({
    title, description, checked, onChange, onClick,
}) => {
    const [css] = useStyletron();

    return (
        <List.Item>
            <div className={css({ width: '100%' })}>
                <div className={css({ float: 'right' })}>
                    <Switch checked={checked} onChange={onChange} onClick={onClick} />
                </div>
                <div className={css({ display: 'inline-block' })}>
                    <strong>
                        {title}
                        :
                    </strong>
                    {' '}
                    {description}
                </div>
                <Clearfix />
            </div>
        </List.Item>
    );
};

ChecklistConfigurationSwitch.propTypes = {
    title: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    checked: PropTypes.bool.isRequired,
    onChange: PropTypes.func,
    onClick: PropTypes.func,
};

ChecklistConfigurationSwitch.defaultProps = {
    onChange: null,
    onClick: null,
};

export const ChecklistConfigureButton = ({ checklistURLManager, filters, selectedFilters }) => {
    const selectedFiltersSet = useMemo(() => new Set(selectedFilters), [selectedFilters]);

    const [isModalVisible, setIsModalVisible] = useState(false);

    const { leftHandedMode, toggleLeftHandedModeHandler } = useChecklistLeftHandedMode();

    const { showModal, handleOk } = useMemo(() => ({
        showModal: () => {
            setIsModalVisible(true);
        },
        handleOk: () => {
            setIsModalVisible(false);
        },
    }), [setIsModalVisible]);

    const filterItems = useMemo(() => {
        const fn = (filterData) => {
            const filterName = filterData.uid;

            const onChange = (checked) => {
                if (checked) {
                    checklistURLManager.addFilter(filterName);
                } else {
                    checklistURLManager.removeFilter(filterName);
                }
            };

            return (
                <ChecklistConfigurationSwitch
                    key={filterName}
                    title={filterData.title}
                    description={filterData.description}
                    checked={selectedFiltersSet.has(filterName)}
                    onChange={onChange}
                />
            );
        };

        return filters
            .map(((filterName) => ChecklistFiltersData[filterName]))
            .map(fn);
    }, [checklistURLManager, filters]);

    return (
        <>
            <Button type="primary" onClick={showModal}>Configure</Button>

            <Modal
                title="Configure"
                visible={isModalVisible}
                onOk={handleOk}
                onCancel={handleOk}
                cancelButtonProps={{ style: { display: 'none' } }}
                maskClosable
                width="50vw"
            >
                <List header={<strong>Interface</strong>}>
                    <ChecklistConfigurationSwitch
                        title="Left Handed Mode"
                        description="Move all checkmarks to the left side of the screen."
                        checked={leftHandedMode}
                        onClick={toggleLeftHandedModeHandler}
                    />
                </List>

                <List header={<><strong>Filters</strong></>}>
                    {filterItems}
                </List>
            </Modal>
        </>
    );
};

ChecklistConfigureButton.propTypes = {
    // eslint-disable-next-line react/forbid-prop-types
    checklistURLManager: PropTypes.object.isRequired,
    filters: PropTypes.arrayOf(PropTypes.string).isRequired,
    selectedFilters: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export const CJ4Checklist = () => {
    const [css] = useStyletron();

    const {
        checklistName, checklistListName, selectedFilters, checklistURLManager,
    } = useChecklistURLConfig();

    const {
        selectedChecklistListName,
        checklistData,
        checklistListData,
        menuItems,
        items,
        resetCallback,
    } = useChecklist({
        checklistName,
        checklistListName,
        selectedFilters,
        checklistURLManager,
    });

    const [menuVisible, setMenuVisible] = useState(false);

    const [menuOpenHandler, menuCloseHandler] = useMemo(
        () => [
            () => setMenuVisible(true),
            () => setMenuVisible(false),
        ],
        [setMenuVisible],
    );

    const checklistMenuItems = useMemo(() => menuItems.map((value) => (
        <Menu.Item key={value.key} onClick={() => { menuCloseHandler(); value.selectMenu(); }}>
            <ChecklistMenuTitle title={value.title} percentProgress={value.progressPercent} />
        </Menu.Item>
    )), [menuItems, menuCloseHandler]);

    const [menuCols, checklistCols] = useWindowWidthConditions([
        { limit: 1000, result: [0, 24] },
        { limit: 1200, result: [7, 17] },
        { limit: 1450, result: [6, 18] },
        { limit: 1800, result: [5, 19] },
        { result: [4, 20] },
    ]);

    const { fullscreenMode, toggleFullscreenModeHandler } = useChecklistFullscreenMode();

    return (
        <div>
            <Modal
                title="List Selector"
                visible={menuVisible}
                onOk={menuCloseHandler}
                onCancel={menuCloseHandler}
                cancelButtonProps={{ style: { display: 'none' } }}
                maskClosable
                width="70vw"
            >
                <Menu
                    mode="inline"
                    // className={css({ height: '100%' })}
                    selectedKeys={[selectedChecklistListName]}
                >
                    {checklistMenuItems}
                </Menu>
            </Modal>

            <div
                className={css({
                    position: 'fixed',
                    bottom: '40px',
                    left: '40px',
                    height: '50px',
                    color: 'white',
                    fontWeight: 'bold',
                    fontSize: '30px',
                    zIndex: '20',
                })}
            >
                {menuCols !== 0 || menuVisible ? null : (
                    // eslint-disable-next-line max-len
                    // eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/interactive-supports-focus
                    <div
                        role="button"
                        className={css({
                            display: 'inline-block',
                            width: '50px',
                            height: '50px',
                            borderRadius: '25px',
                            backgroundColor: '#237ad0',
                            paddingLeft: '10px',
                            paddingTop: '1px',
                            marginRight: '10px',
                        })}
                        onClick={menuOpenHandler}
                    >
                        <UnorderedListOutlined />
                    </div>
                )}

                {!fullscreenMode || menuVisible ? null : (
                    // eslint-disable-next-line max-len
                    // eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/interactive-supports-focus
                    <div
                        role="button"
                        className={css({
                            display: 'inline-block',
                            width: '50px',
                            height: '50px',
                            borderRadius: '25px',
                            backgroundColor: '#5d5d5d',
                            paddingLeft: '10px',
                            paddingTop: '1px',
                        })}
                        onClick={toggleFullscreenModeHandler}
                    >
                        <FullscreenExitOutlined />
                    </div>
                ) }
            </div>

            {fullscreenMode ? null : (
                <>
                    <div>
                        <div className={css({ float: 'right' })}>
                            <Button
                                className={css({ fontWeight: 'bold' })}
                                onClick={toggleFullscreenModeHandler}
                            >
                                <FullscreenOutlined />
                            </Button>
                            {' '}
                            <ChecklistConfigureButton
                                checklistURLManager={checklistURLManager}
                                filters={checklistData.selectableFilters}
                                selectedFilters={selectedFilters}
                            />
                            {' '}
                            <Button type="danger" onClick={resetCallback}>Reset</Button>
                        </div>
                        <Title>
                            {checklistData.title}
                        </Title>
                        <Clearfix />
                    </div>

                    <div>
                        <Text>{checklistData.description}</Text>
                        <Divider />
                    </div>
                </>
            )}
            <Row>
                <Col span={menuCols}>
                    <Menu
                        mode="inline"
                        className={css({ height: '100%' })}
                        selectedKeys={[selectedChecklistListName]}
                    >
                        {checklistMenuItems}
                    </Menu>

                </Col>
                <Col span={checklistCols}>
                    {menuCols !== 0 ? null : (
                        <Title level={3}>{checklistListData.title}</Title>
                    )}

                    <div style={{ padding: '0 20px' }}>
                        {items.map((item) => <ChecklistItem key={item.uid} item={item} />)}
                    </div>
                </Col>
            </Row>
        </div>
    );
};
